<template>
  <div class="buttom" v-show="todos.length">
          <input type="checkbox" name="" id="" :checked="doneTotal ===todos.length && todos.length >0" style="display:none" />
          <span>已完成{{doneTotal}} / 未完成{{todos.length-doneTotal}} /『All is {{todos.length}}』</span>
          <button @click="clearAll">清除选中</button>
        </div>
</template>

<script>
export default {
    name:'footerVue',
    props:['todos','clearTodo'],
    computed:{
     doneTotal(){
   return   this.todos.reduce((pre,current)=>{
     
       return pre +(current.done?1:0)
      },0)
     }
    },
    methods:{
      clearAll(){
        this.clearTodo()
      }
    }
}
</script>

<style>
.root .content .list .buttom {
  width: 575px;

  margin-left: 12px;
  margin-top: 10px;
  line-height: 36px;
  margin-bottom: 10px;
  vertical-align: baseline;
}
.root .content .list .buttom span {
  margin-left: 10px;
  color: #b2b2b2;
}
.root .content .list .buttom button {
  float: right;
  background-color: rgba(249, 83, 83, 0.619);
  border: none;
  width: 100px;
  height: 30px;
  border-radius: 5px;
}
</style>