<template>
  <div class="root">
    <div class="content">
      <div class="list">
   <headerVue :receive="receive"/>
       <listVue :todos="todos" :checkTodo="cheackTodo" :delete2="delete2"/>
        <fonterVue :todos="todos" :clearTodo="clearTodo"></fonterVue>
        
      </div>
    </div>
  </div>
</template>

<script>
import fonterVue from './components/footerVue.vue';
import headerVue from './components/headerVue.vue';
import listVue from './components/listVue.vue';


export default {
  name: "App",
  components: {
    headerVue,listVue,fonterVue
  },
  data(){
        return {
           
                todos:JSON.parse(localStorage.getItem('todos')) || []
            
        }
    },
    methods:{
      receive(x) {
       this.todos.unshift(x)
      },
      cheackTodo(id){
        this.todos.forEach((todo)=>{
          if(todo.id === id) {
            todo.done = !todo.done
          }
        })
      },
      delete2(id){
      this.todos =  this.todos.filter((todo)=>{
          return todo.id !== id
        })
      },
      clearTodo(){
     this.todos=   this.todos.filter((todo)=>{
          return !todo.done
        })
      }
      
    },
    watch:{
      todos:{
        deep:true,
       handler(value){
         localStorage.setItem('todos',JSON.stringify(value))
       }
      }
    }
};
</script>

