<template>
  <div class="top">
          <input type="text" maxlength="10"  v-model="input" :placeholder="plo"  @keydown.enter="add"/>
        
         
        </div>
</template>

<script>
import {nanoid} from 'nanoid'
export default {
    name:'headerVue.vue',
    data(){
      return {
        input:'',
        plo:this.getOne()
      }
    },
  props:['receive'],
  
    methods:{
        add(){
            const todoObj = {id:nanoid(),title:this.input,timer:'' ,done:false}
          
            this.receive(todoObj)
            this.input = ''
        },
        getOne(){
      this.$http.get('https://v1.hitokoto.cn/')
          .then(({data})=>{
          this.plo=  data.hitokoto+'--'+ (data.from_who==null?(data.c==null?data.from:data.c):data.from_who)
          })
          .catch(console.error)
        },
       
        
    },
    
   
}
</script>

<style>
.root .content .list .top {
  margin-left: 12px;
  margin-top: 10px;
}
.root .content .list .top input {
  width: 570px;
  height: 36px;
  background-color: rgba(251, 251, 226, 0.412);
 
 text-align: left;
  border: 1px solid #ccc;
  border-radius: 5px;
   outline: none;
  font-size: 14px;
}
.root .content .list .top input:hover {
  background-color: rgba(255, 255, 255, 0.518);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  
  
}
.root .content .list .top input::placeholder {
  
  font-size: 14px;
  text-align: center;
  font-weight: 500;
}
.root .content .list .top input:focus::placeholder{
        opacity: 0;
    }



</style>